.ringPlanet {
	width: 100%;
	margin: 0 auto;
}
.ringPlanet-1 {
	width: 100%;
	justify-content: center;
}

#bg-planet {
	background-color: #eaeaeb;
	border: none;
	color: #121c33;
}

.card.planetsBox {
	/* width: 250px;
  height: 250px; */
	border-radius: 16px;
	box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.1);
}

.card-body > .fs-6.card-title.h5 {
	font-weight: 600;
	line-height: 24px;
	align-items: center;
	font-family: 'Poppins', sans-serif;
	color: #121c33;
}

.card-body .card-text {
	color: #121c33;
	opacity: 0.6;
	font-family: 'Poppins', sans-serif;
	font-size: 12px;
}

.noEmptySpace {
	font-weight: 900;
	font-size: 25px;
	margin-top: 40px;
}

.createPlanetBtn {
	background: #121c33;
	color: #fff;
	font-weight: 600;
	padding: 0.5rem 1.3rem;
	margin-top: 30px;
	border: none;
	border-radius: 6px;
	text-transform: uppercase;
}

.navy-color.modalTitle.modal-title {
	font-weight: 900;
	font-size: 40px;
}

.modal-dialog>.modal-content {
	height: 90%;
  border: 5px ;
  border-radius: 22px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 1.2rem;
}

.nav-tabs>.nav-item>.nav-link{
  color: #121c33;
  background-color:rgba(18, 28, 51, 0.05);
  font-weight: 600;
  margin: 0 10px 0 0;
  border: none;


}
.nav.nav-tabs{
	border-bottom: none;
}

#planet-body.card-body{
  margin-left: 0.9rem;
}

#planet-sidebar{
  width: 20rem;
  margin-top: 0.5rem;
  margin-bottom:0.5rem ;
  border-radius: 20px;
  box-shadow: 0px 8px 16px 0px #00000033;

}

.side-bar-font{
  font-size: 16px;
}

.side-bar-smaller-font{
  font-size: 11px;
}

.modal-content>.modal-header{
  border-bottom: none;
}

div.modal-footer{
  border-top: none;
}